
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Previewer from 'vux/src/components/previewer/index.vue'

import wx from 'weixin-js-sdk';
import weixin from '@/services/weixin';
import axios from "axios";

export default {
components: {
XHeader,
Flexbox,
FlexboxItem,
Previewer
},
directives: {
clipping: {
inserted: (el, binding) => {
const img = new Image();
img.onload = () => {
if (img.width > img.height) {
el.style.height = '100%';
} else {
el.style.width = '100%';
}
el.src = binding.value;
};
img.src = binding.value;
}
},
TransferDom
},
data() {
return {
list: [],
imgs: [],
roleid: this.$store.getters.user.roleid,
partyBranch1:'',
topShow:false,
showTrans:false,
department:'',
departmentids:'',
activeS:false,
activesId:1
};
},
mounted() {
this.getlist1();
weixin.init(['chooseImage', 'uploadImage']);

},
methods: {
change(park){
this.activesId = park.departmentid;
this.partyBranch1 = park.departmentname;
this.departmentids=park.departmentid;
this.partyBranch=park.partyBranch;
this.topShow = !this.topShow;
this.showTrans = !this.showTrans;
this.getList();
},
getlist1(){
axios({
method: 'get',
url: 'pdepartment/getList',
}) .then((res)=> {
this.department=res.data;
console.log(res.data)
this.partyBranch1 = this.department[0].departmentname;
this.departmentids= this.department[0].departmentid;
this.getList();
axios({
method: 'get',
url: 'ppartymember/getPartymemberByDepartmentid',
params: {
departmentid:this.departmentids,
status:1
}
}).then((res)=> {
this.length2=res.data.length;
}).catch(function (error) {
console.log(error);
});
}).catch(function (error) {
console.log(error);
})
},
showDet(){
this.topShow = !this.topShow;
this.showTrans = !this.showTrans;
},
datePick(s) {
Date.prototype.toLocaleString = function() {
return this.getMonth() + 1 + '月' + this.getDate() + '日 ';
};
return new Date(s).toLocaleString();
},
preview(index, idx) {
this.$refs.previewer[index].show(idx);
},
getList() {
this.$http
.get('active/getRunningActive', {
params: {
pageNum: 1,
pageSize: 200,
departmentid: this.departmentids,
userId: this.$store.getters.user.userid
}
})
.then(res => {
this.list = res.data.list;
this.list.forEach((item, index) => {
item.previewerClassName = `previewer-${index}-img`;
item.previewerOptions = {
getThumbBoundsFn(idx) {

let thumbnail = document.querySelectorAll(`.${item.previewerClassName}`)[idx];

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop;


let rect = thumbnail.getBoundingClientRect();

return { x: rect.left, y: rect.top + pageYScroll, w: rect.width };


}
};
item.pictures = item.pictures || [];
item.startTime = item.startTime;
item.pictures.forEach(item => {
item.src = 'http://www.dlbdata.cn/dangjian/picture/showThumbnail?pictureId=' + item.pictureId;
});
});
});
},
chooseImage(item) {

const limit = 9 - item.pictures.length;
if (limit) {
wx.chooseImage({
count: limit, // 默认9
sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
success: res => {
let localIds = res.localIds || [];
new Promise(resolve => {
let serverIds = [];
let toUpload = localId =>
wx.uploadImage({
localId: localId, // 需要上传的图片的本地ID，由chooseImage接口获得
isShowProgressTips: 1, // 默认为1，显示进度提示
success: res => {
serverIds.push(res.serverId);
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}
});
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}).then(serverIds => this.uploadImage(item, serverIds));
}
});
}
},
uploadImage(item, serverIds) {
let promiseList = [];
serverIds.map(serverId =>
promiseList.push(
this.$http.get('picture/upload', {
params: {
mediaId: serverId
}
})
)
);
Promise.all(promiseList).then(results =>
results.map(result => {
item.pictures.push({
src: 'http://www.dlbdata.cn/dangjian/picture/showThumbnail?pictureId=' + result.data
});
axios({
url: "active/savePicture",
method: 'post',
headers: {'contentType': 'application/x-www-form-urlencoded'},
params: {
activeId: item.id,
pictureId: result.data
}
}).then(res => {
this.$vux.alert.show({title:  res.msg});
}).catch(err => {
this.$vux.alert.show({title: '提交失败'});
});
})
);
}
},
};



